"use client";

import type { InputHTMLAttributes } from "react";

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  label?: string;
}

export function Input({ label, className = "", id, ...props }: InputProps) {
  const inputId = id || label?.toLowerCase().replace(/\s+/g, "-");
  return (
    <div className="flex flex-col gap-1.5">
      {label && (
        <label htmlFor={inputId} className="text-xs text-white/50 font-medium">
          {label}
        </label>
      )}
      <input
        id={inputId}
        className={`bg-white/5 border border-surface-border rounded-input px-3 py-2 text-sm text-white placeholder:text-white/30 focus:outline-none focus:border-accent-blue/50 focus:ring-1 focus:ring-accent-blue/20 transition-colors ${className}`}
        {...props}
      />
    </div>
  );
}
